  opacity: 60%;
}
.main-navigation--closed .main-navigation__toggle::before:active {
  opacity: 30%;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 27px;
  right: 17px;
  width: 32px;
  height: 4px;
  background-color: #FFFFFF;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
